.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: $header-size;

  a {
    text-decoration: none;
    color: $white;
  }

  &-item {
    padding: 0 2rem;

    @include min-mq(large) {
      & + & {
        border-left: 1px solid grey;
      }
    }
  }

  @include max-mq(large) {
    background: $rhino;
    transform: translateX(150%);
    transition: all .5s;
  }
}

.js-show-nav {
  transform: translateX(0%);
  width: 100%;
  height: 100vh;

  &.nav {
    flex-direction: column;

    .nav-item {
      margin: 3rem;
    }
  }
}

